<template>
  <div
    v-if="isAllReady"
    class="SurveyPassing"
  >
    <div class="header">
      <img
        :src="imagePath"
        alt="Обложка"
        class="image"
      >

      <div class="gradient">
        <div class="inner">
          <div
            v-if="survey.type"
            class="tag tag-type"
          >
            {{ surveyType }}
          </div>
          <div class="name">
            <h1>{{ survey.name }}</h1>
          </div>
          <div
            v-if="survey.description"
            class="survey-description"
          >
            <div class="text">{{ survey.description }}</div>
          </div>
        </div>
      </div>
      <div
        v-if="survey.color"
        class="border"
      />
    </div>

    <el-alert
      v-if="isCompleted"
      :closable="false"
      title="Вы уже прошли опрос"
      type="success"
      center
      show-icon
      class="alert-is-completed"
    />

    <el-alert
      v-if="notAuthorized"
      :closable="false"
      title="Зарегистрируйтесь для прохождения опроса"
      type="warning"
      center
      show-icon
      class="alert-is-completed"
    />

    <div
      v-loading="isFinishing"
      :class="['container', {
        'is-completed': isCompleted,
        'not-authorized': notAuthorized
      }]"
    >
      <div class="inner">
        <div
          v-if="!isCompleted && survey.start_message"
          class="start-message"
        >
          {{ survey.start_message }}
        </div>

        <div
          v-loading="isSending[question.id]"
          v-for="(question, index) in survey.questions"
          :key="question.id"
          class="question"
        >
          <div class="title">
            <div class="index">{{ index + 1 }}.</div>
            <p class="description">{{ question.description }}</p>
          </div>

          <template v-if="question.multiple">
            <div class="tip">
              Можно выбрать от {{ question.min }} до {{ question.max }} вариантов
            </div>

            <el-checkbox
              v-for="option in question.options"
              :key="option.id"
              v-model="option.selected"
              :class="['option', { 'owned': hasOwn(option) }]"
              :disabled="isCompleted || hasDisabledCheckbox(question, option)"
              border
              @change="checkboxChanged({ question })"
            >
              <template v-if="isStats">
                <div
                  :style="{ width: getStatsPercent(question, option) }"
                  class="progress"
                />

                <div class="stats">
                  <!-- Отобразится если сверху будет остановлен класс .owned -->
                  <i class="icon el-icon-check" />
                  <div class="percent">{{ getStatsPercent(question, option) }}</div>
                </div>
              </template>

              <h3
                v-if="option.description"
                class="description"
              >
                <div>{{ option.description }}</div>

                <div
                  v-if="option.total"
                  class="total"
                >
                  <div class="dot">.</div>
                  <div>{{ option.total }}</div>
                </div>
              </h3>

              <div
                v-if="option.hint"
                class="hint"
              >
                {{ option.hint }}
              </div>

              <el-input
                v-if="option.input_field"
                :disabled="isCompleted || !option.selected"
                :maxlength="option.input_field_size"
                v-model.lazy="option.input_text"
                type="textarea"
                autosize
                placeholder="Введите Ваш комментарий"
                class="input-text"
                @change="send({ question })"
              />
            </el-checkbox>
          </template>

          <div
            v-for="option in question.options"
            v-else
            :key="option.id"
          >
            <el-radio
              v-model="radios[question.id]"
              :label="option.id"
              :disabled="isCompleted"
              :class="['option', { 'owned': hasOwn(option) }]"
              border
              @change="radioChanged({ question, option })"
            >
              <template v-if="isStats">
                <div
                  :style="{ width: getStatsPercent(question, option) }"
                  class="progress"
                />

                <div class="stats">
                  <!-- Отобразится если сверху будет остановлен класс .owned -->
                  <i class="icon el-icon-check" />
                  <div class="percent">{{ getStatsPercent(question, option) }}</div>
                </div>
              </template>

              <h3
                v-if="option.description"
                class="description"
              >
                <div>{{ option.description }}</div>

                <div
                  v-if="option.total"
                  class="total"
                >
                  <div class="dot">.</div>
                  <div>{{ option.total }}</div>
                </div>
              </h3>

              <div
                v-if="option.hint"
                class="hint"
              >
                {{ option.hint }}
              </div>

              <template v-if="option.input_field">
                <!-- :disabled - для 1 radio, будет разрешён ввод в input. -->
                <!-- Фокус в input будет провоцировать выделение этого 1 radio. -->
                <el-input
                  v-if="option.input_field_type === 'string'"
                  :disabled="question.options.length !== 1 && !option.selected"
                  :maxlength="option.input_field_size"
                  :placeholder="getInputPlaceholder(option)"
                  v-model.lazy="option.input_text"
                  type="textarea"
                  autosize
                  class="input-text"
                  @focus="specialFocused({ question, option })"
                  @change="specialSend({ question, option })"
                />

                <el-date-picker
                  v-else-if="option.input_field_type === 'date'"
                  :disabled="question.options.length !== 1 && !option.selected"
                  v-model="option.input_text"
                  type="date"
                  placeholder="Укажите дату"
                  class="date-picker"
                  @focus="specialFocused({ question, option })"
                  @change="specialSend({ question, option })"
                />

                <!-- todo потом вернуть это. сервер требует строку, а летит число. -->
                <!--v-model="option.input_text"-->
                <el-input-number
                  v-else-if="option.input_field_type === 'number'"
                  :disabled="question.options.length !== 1 && !option.selected"
                  :min="0"
                  :max="option.input_field_size"
                  :value="Number(option.input_text)"
                  @focus="specialFocused({ question, option })"
                  @change="send({ question, option, isInputNumber: true, value: $event })"
                />
              </template>

              <input
                v-if="option.attach_file"
                :disabled="isCompleted"
                type="file"
                accept="*"
                @change="fileChanged(question, $event)"
              >
            </el-radio>
          </div>
        </div>

        <div
          v-show="isAllReady && !isCompleted"
          class="finish"
        >
          <div
            v-if="survey.finish_message"
            class="finish-message"
          >
            {{ survey.finish_message }}
          </div>

          <el-button
            :loading="isFinishing"
            type="primary"
            class="complete"
            @click="finish"
          >
            Закончить
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'SurveyPassing',

  metaInfo() {
    return {
      title: this.survey.name,
    };
  },

  props: {
    id: {
      type: [Number, String],
      default: -1,
    },
  },

  data() {
    return {
      isAllReady: false,
      survey: {},
      isCompleted: false,
      isStats: false,
      notAuthorized: false,

      // Обслуживание единого выбора из нескольких для radio элементов.
      // Структура: { questionId: optionId, .. }
      radios: {},

      isSending: {},
      isFinishing: false,
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),

    imagePath() {
      return this.survey.cover_url
        ? `${process.env.VUE_APP_API}${this.survey.cover_url}`
        : require('@/assets/trianglify.svg');
    },

    surveyType() {
      return this.$store.getters['surveys/getTypeByKey'](this.survey.type)?.value;
    },
  },

  async mounted() {
    await this.checkAuth();

    this.restoreRadioSelection();
    this.isAllReady = true;

    this.$nextTick(this.setGeneralColor);
  },

  methods: {
    // Опрос визуально пройден - блокирован, для не авторизованных пользователей.
    async checkAuth() {
      if (this.current_user) {
        await this.getSurvey(`/surveys/${this.id}/responses`);
        this.isCompleted = Boolean(this.survey.completed_at);
        this.isStats = this.isCompleted && this.survey.public_stats;

        if (this.isStats) {
          await this.getStatistic();
        }
      } else {
        this.notAuthorized = true;
        await this.getSurvey(`/surveys/${this.id}`);
      }
    },

    async getSurvey(path) {
      try {
        const { data } = await this.$http.get(path);
        this.survey = data;
      } catch (err) {
        console.error('SurveyPassing / getSurvey', err);
      }
    },

    restoreRadioSelection() {
      this.survey?.questions?.forEach((question) => {
        question.options.some((option) => {
          if (option.selected) {
            this.radios[question.id] = option.id;
            return true;
          }

          return false;
        });
      });
    },

    // Полностью заменяет опрос, но получается как будто подмешивает статистику.
    async getStatistic() {
      try {
        const { data } = await this.$http.get(`/surveys/${this.id}/statistic`);

        // Крепление количества всех ответов к вопросу.
        data.questions.forEach((question) => {
          /* eslint-disable no-param-reassign */
          question.total = question.options.reduce((sum, option) => {
            sum += option.total;
            return sum;
          }, 0);

          return question;
        });

        this.survey = data;
      } catch (err) {
        console.error('SurveyPassing / getStatistic', err);
      }
    },

    setGeneralColor() {
      if (this.survey.color) {
        this.$el.style.setProperty('--general-color', this.survey.color);
      }
    },

    getInputPlaceholder({ input_field_size: size }) {
      return `Введите Ваш комментарий.. (максимум символов: ${size})`;
    },

    hasOwn(option) {
      return option.question_responses?.some(res => this.current_user.bio_id === res.bio_id);
    },

    getStatsPercent(question, option) {
      return `${(option.total / question.total * 100).toFixed(2)}%`;
    },

    // Баг в element el-checkbox-group:
    // С установкой min > 2 - нельзя было выбрать вариант ответа.
    // Принятая мера (в hasDisabledCheckbox):
    // * С уже выбранными полями можно взаимодействовать.
    // * Если уже выбрано дозволенное количество (=max), то НЕ выбранные поля блокируются.
    hasDisabledCheckbox(question, option) {
      // Минимальное значение не влияет на блокирование поля, чтобы можно было менять свои решения.
      // Пример: когда можно выбрать 1 из 4, если уже выбрано значение, перевыбрать будет невозможно
      // если min будет указан, но при этом это только визуальное решение - данные на сервер не
      // будут отправлены, если не будет удовлетворять min <> max.
      const { /* min, */ max, options } = question;

      const countSelected = options.reduce((count, { selected }) => {
        if (selected) {
          /* eslint-disable no-param-reassign */
          count += 1;
        }

        return count;
      }, 0);

      // Приоритет выше.
      if (option.selected) {
        return false;
      }

      return countSelected >= max;
    },

    checkboxChanged({ question }) {
      const { min, max, options } = question;

      const count = options.reduce((acc, { selected }) => {
        if (selected) {
          /* eslint-disable no-param-reassign */
          acc += 1;
        }

        return acc;
      }, 0);

      if (count >= min && count <= max) {
        this.send({ question });
      }
    },

    radioChanged({ question, option }) {
      question.options.forEach((opt) => { opt.selected = false; });
      option.selected = true;

      this.send({ question });
    },

    // todo доделать + реакция на loaded.
    async fileChanged(question, { target }) {
      const [file] = target.files;
      const reader = new FileReader();

      if (file) {
        reader.onloadend = () => {
          console.log('reader.result', reader.result);
          // this.send({ question });
        };

        reader.readAsDataURL(file);
      }
    },

    specialFocused({ question, option }) {
      if (question.options.length === 1) {
        this.$set(this.radios, question.id, option.id);
      }
    },

    async specialSend({ question, option }) {
      this.$set(this.isSending, question.id, true);

      if (question.options.length === 1) {
        option.selected = true;
      }

      try {
        await this.$http.post(`/surveys/${this.id}/responses`, {
          survey_response: { survey: this.survey },
        });
      } catch (err) {
        console.error('SurveyPassing / preSend', err);
      } finally {
        this.$set(this.isSending, question.id, false);
      }
    },

    // todo value / option / isInputNumber временный
    async send({ question, complete = false, option, isInputNumber = false, value = '' }) {
      this.$set(this.isSending, question.id, true);

      // todo del
      if (isInputNumber) {
        option.input_text = String(value);
      }

      try {
        await this.$http.post(`/surveys/${this.id}/responses`, {
          survey_response: { complete, survey: this.survey },
        });
      } catch (err) {
        console.error('SurveyPassing / send', err);
      } finally {
        this.$set(this.isSending, question.id, false);
      }
    },

    // todo проверка, что все поля заполнены.
    async finish() {
      try {
        this.isFinishing = true;
        // Есть общий прелоадер, а этот нужно корректно заглушить.
        await this.send({ question: { id: undefined }, complete: true });

        if (this.survey.public_stats) {
          this.checkAuth();
        } else {
          this.$router.push({ name: 'Surveys' });
          this.$message.success('Вы успешно прошли опрос.');
        }
      } catch (err) {
        console.error('SurveyPassing / finish', err);
      } finally {
        this.isFinishing = false;
      }
    },
  },
};
</script>

